import LbPicker from '@/pagesTwo/base/lb-picker'
var app = getApp().globalData
export default {
	components: {
	    LbPicker
	},
	data() {
		return {
			n:0,
			classifyList:['平台活动','俱乐部活动'],
			time: new Date().getTime(),
			page:1,
			hotList:[],
			loading:false,
			allowloading:false,
			city_name: '全部',
			choose_city: '',
			inside_list: [], // 国内城市列表
			outside_list: [], // 国外城市列表
			countryList:[],
			myProps: {
			  label: 'name',
			  value: 'id',
			  children: 'child'
			},
			showTitle:false
		}
	},
	onLoad() {
		this.getActivity()
	},
	onShareAppMessage:function(res) {
		return {
		  title: '德申汇活动',
		  imageUrl:app.shareImg,
		  path: 'pagesTwo/Activity/Activity'
		}
	},
	onPullDownRefresh(){
		this.page = 1
		this.getActivity()
		setTimeout(function () {
		        uni.stopPullDownRefresh();
				uni.showToast({
				    title: '刷新成功',
				    duration: 1000,
					icon:'none'
				});
		  }, 1000);
	},
	onReachBottom(){
		if(this.allowloading){
			this.allowloading = false
			this.loading = true
			this.page++
			this.getActivity()
			// setTimeout(() => {
			// 	this.page++
			// 	this.getActivity()
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			// }, 1500)	
		}
	},
	methods: {
		goDetail(id){
			uni.navigateTo({
				url:'../../pagesTwo/ActivityDetail/ActivityDetail?id='+id
			})
		},
		picok(e){
			// console.log(e.item[1].name)
			this.city_name = e.item[1].name
			this.$nextTick(()=>{
				this.page = 1
				this.getClub()
			})
		},
		pic(){
			this.$refs.picker.show()
		},
		classifyChange(ind,id){
			uni.pageScrollTo({
				scrollTop:0
			})
			setTimeout(()=>{
				this.n = ind
				this.page = 1 
				this.getActivity()
			}, 200);
			
			
		},
		getActivity () {
		  if (this.n === 0) {
		    this.getStage()
		  } else if (this.n === 1) {
		    this.getClub()
		    this.getCity()
		  }
		},
		getStage(){
			this.$api.activity_public_list({
			  token: uni.getStorageSync("token"),
			  user_id: uni.getStorageSync("userId"),
			  page: this.page
			}).then(res => {
				// console.log(res)
				if(res.data.code=== '200'){
					if(this.page===1){
						
						this.hotList = res.data.data
						if(res.data.data.length>=10){
							this.allowloading= true
							this.loading =false
							this.showTitle =false
						}else{
							this.allowloading= false
							this.loading =false
							this.showTitle = true
						}
					}else{
						this.hotList = this.hotList.concat(res.data.data)
						if(res.data.data.length>=10){
							this.allowloading= true
							this.loading =false
							this.showTitle =false
						}else{
							this.allowloading= false
							this.loading =false
							this.showTitle = true
						}
					}
					
					for (let j = 0; j < this.hotList.length; j++) {
					  if (new Date(this.hotList[j].finish_date.replace(/-/g, '/')).getTime() > this.time) { // 结束时间大于当前时间
					    this.hotList[j].state = true
					  } else {
					    this.hotList[j].state = false
					  }
					}
					
				}
			})
		},
		getClub(){
			this.$api.activity_private_list({
			  token: uni.getStorageSync("token"),
			  user_id: uni.getStorageSync("userId"),
			  page: this.page,
			  city_name:this.city_name == '全部'?'':this.city_name
			}).then(res => {
				// console.log(res)
				if(res.data.code=== '200'){
					if(this.page===1){
						
						this.hotList = res.data.data
						if(res.data.data.length>=10){
							this.allowloading= true
							this.loading =false
							this.showTitle =false
						}else{
							this.allowloading= false
							this.loading =false
							this.showTitle =true
						}
					}else{
						this.hotList = this.hotList.concat(res.data.data)
						if(res.data.data.length>=10){
							this.allowloading= true
							this.loading =false
							this.showTitle =false
						}else{
							this.allowloading= false
							this.loading =false
							this.showTitle =true
						}
					}
					
					for (let j = 0; j < this.hotList.length; j++) {
					  if (new Date(this.hotList[j].finish_date.replace(/-/g, '/')).getTime() > this.time) { // 结束时间大于当前时间
					    this.hotList[j].state = true
					  } else {
					    this.hotList[j].state = false
					  }
					}
					
				}
			})
		},
		getCity () {
		  this.$api.city_choose().then(res => {
		    if (res.data.code === '200') {
				// console.log(res)
		      this.inside_list = res.data.data.cn
		      this.outside_list = res.data.data.other
		      let country = new Array(1)
		      country[0] = ({name: '全部',child: [{name: '全部'}]})
		      country[1] = ({name: '国内', child: this.inside_list})
		      country[2] = ({name: '国外', child: this.outside_list})
		      this.countryList = country
		      // console.log(this.countryList)
		      // if (this.countryList) {
		      //   this.myAddressSlots[0].values = this.getProvinceArr()
		      //   this.myAddressSlots[2].values = this.getCityArr('全部')
		      // }
		    }
		  })
		}
	}
}
